import React, { useState, useEffect } from 'react';
import { Redirect, NavLink } from "react-router-dom";
import {
  Container, Grid, TextField, Typography, Button,
  MenuItem,Paper
} from "@material-ui/core";

import { Form, Formik } from 'formik';
import * as Yup from 'yup';
import { format } from "date-fns";
import { requesicoes } from '../../../utils/services';
import { Alert, Autocomplete, Stack } from '@mui/material';
import { makeStyles } from '@material-ui/core/styles';
import AdapterDateFns from '@material-ui/lab/AdapterDateFns';
import LocalizationProvider from '@material-ui/lab/LocalizationProvider';
import DesktopDatePicker from '@material-ui/lab/DesktopDatePicker';
import Swal from 'sweetalert2';
import { MoonLoader } from 'react-spinners';
//import Stack from '@material-ui/core/Stack';
//import { format } from "date-fns";


let provinciasde = [];
let cursos = [];
let sexos = [];
let niveisAcademicos = [];
let tiposDocumentos = [];
let cadeiras = [];
let nacionalidades = [];
let provincias = [];
let cidades = [];
let distritos = [];
let cadeirasDepartamento = [];
let departamentos = [];
let regimesServivo = [];
let estados = [];
const MAX_SELECTIONS = 2; // Defina o número máximo de seleções permitidas



const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    '& > * + *': {
      marginTop: theme.spacing(2),
    },
  },
}));


export default function NovoDocente(props) {

  const [to, setTo] = useState('');
  const [redirect, setRedirect] = useState(false);
  const [updateDom, setUpdateDom] = useState(false);
  const [provincia, setProvincia] = useState('');
  const [menuOpen, setMenuOpen] = useState(false);
  const [selectedOptions, setSelectedOptions] = useState([]);
  const [showAlert, setShowAlert] = useState(false);
  const [showSucess, setShowSucess] = useState(false);
  const [selectedDateNasc, setSelectedDateNasc] = useState(new Date());
  const [selectedDateEmissao, setSelectedDateEmissao] = useState(new Date());
  const [carregando, setCarregando] = useState(true);



  useEffect(() => {
    init();
    setTimeout(() => {
      setCarregando(false);
    }, 2000);
  }, [])

  async function init() {
    setCarregando(true);

    const [cursosResquest, cadeirasRequest,
      departamentoRequest, regimesServicoRequest,
      sexosRequest, tipoDocRequest, estadoRequest, nivelAcademicoRequest, paisRequest, provinciaRequest, cidadeRequest
    ] = await Promise.all([requesicoes().buscarCurso(), requesicoes().buscarCadeiras(), requesicoes().buscarDepartamentos(),
    requesicoes().buscarRegimes(), requesicoes().buscarSexo(), requesicoes().buscarDocumento(), requesicoes().buscarSituacao(),
    requesicoes().buscarNiveis(), requesicoes().buscarPaises(), requesicoes().buscarProvincia(), requesicoes().buscarCidades()]);

    cursos = cursosResquest.data;
    regimesServivo = regimesServicoRequest.data;
    cadeiras = cadeirasRequest.data;
    departamentos = departamentoRequest.data;
    sexos = sexosRequest.data;
    niveisAcademicos = nivelAcademicoRequest.data;
    tiposDocumentos = tipoDocRequest.data;
    console.log(tiposDocumentos, "Documentos")
    estados = estadoRequest.data;
    nacionalidades = paisRequest.data;
    provincias = provinciaRequest.data;
    distritos = cidadeRequest.data;
    
    setCarregando(false);

    setUpdateDom(!updateDom);
  }

  async function buscarProvincias(v) {

    provinciasde = provincias.filter(p => p.country_id.id == v.id)
    setProvincia(provinciasde)
    console.log(provinciasde, 'Provincias')


  }

  async function buscarCidades(v) {
    cidades = distritos.filter(d => d.regiaoPai.id == v.id)
  }

  const handleOptionSelect = (event) => {
    const { value } = event.target;

    // Verifica se o número máximo de seleções já foi atingido
    if (selectedOptions.length === MAX_SELECTIONS && !selectedOptions.includes(value)) {
      setShowAlert(true);
      return;
      //window.alert('Atingiu o numero maximo de cadeica a lecionar!');
    }

    // Atualiza a lista de opções selecionadas
    setSelectedOptions(event.target.value);
    setShowAlert(false); // Esconde o alerta quando uma opção válida for selecionada

  };


  if (redirect)
    return (<Redirect to={to} />)
  else
    return (<div style={{ paddingRight: '10px' }}>
      {carregando ? (
        <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', width: '100%', marginTop: '20%' }}>
          <MoonLoader color="#36d7b7" />
        </div>

      ) 
      :
      (<Container>
        <Paper>
          {showAlert && (
            <Alert severity="warning">
              Limite máximo de cadeiras alcançado. Você pode selecionar no máximo {MAX_SELECTIONS} cadeiras.
            </Alert>
          )}

          {showSucess && (
            <Alert severity="success">Docente cadastrado com sucesso!</Alert>

          )}
          {/* <Alert severity="warning">Atingiu o numero maximo de cadeica a lecionar!</Alert> */}
          <Container><br />
            <Typography variant="h6">Novo Docente</Typography><br />
            <Formik
              initialValues={{}}
              onSubmit={(values, { setSubmitting }) => {
                async function save(values) {
                  try {
                    //values.dataNascimento = format(values.dataNascimento, 'yyyy-MM-dd');
                    console.log(JSON.stringify(values), 'Dados')
                    await Promise.resolve(requesicoes().postProfessor([values]));
                    Swal.fire(
                      'Exito!',
                      'Docente cadastrado com sucesso!',
                      'success'
                    )
                    setTo('/teacher/list');
                    setRedirect(true);
                  } catch (e) {
                    if (e.response.status == 400) {
                      Swal.fire({
                        icon: 'alert',
                        title: 'Oops...',
                        text: e.response.data.mensagemUsuario,
                      })
                    } else {
                      Swal.fire({
                        icon: 'error',
                        title: 'Oops...',
                        text: e.response.data.message,
                      })

                    }
                  }
                }
                save(values);
                setTimeout(() => {
                  setSubmitting(false);
                }, 500);
              }}

              validationSchema={Yup.object().shape({

                /* nome: Yup.string().required('Campo obrigatório'),
                 apelido: Yup.string().required('Campo obrigatório'),
                 sexo: Yup.string().required('Campo obrigatório'),
                 departamento: Yup.string().required('Campo obrigatório'),
                 regime: Yup.string().required('Campo obrigatório'),
                 nivelAcademico: Yup.string().required('Campo obrigatório'),*/

              })}>
              {props => {
                const {
                  values, touched, errors, dirty, isSubmitting,
                  handleChange, handleBlur, handleSubmit, handleReset, setFieldValue
                } = props;
                return (<Form>
                  <Grid container spacing={2}>
                    <Grid item sm={4}>
                      <TextField
                        type="text" variant="outlined" name="apelido"
                        error={(errors.apelido && touched.apelido)}
                        helperText={errors.apelido}
                        onChange={handleChange}
                        onBlur={handleBlur}
                        value={values.apelido} margin="dense"
                        id="nome" fullWidth label="Apelido (*)" />
                    </Grid>
                    <Grid item sm={4}>
                      <TextField
                        type="text" variant="outlined" name="nome"
                        error={(errors.nome && touched.nome)}
                        helperText={errors.nome}
                        onChange={handleChange}
                        onBlur={handleBlur}
                        value={values.nome} margin="dense"
                        id="nome" fullWidth label="Nome Completo (Sem apelido)" />
                    </Grid>
                    <Grid item sm={4}>
                      <TextField
                        type="text" variant="outlined" name="nacionalidade"
                        error={(errors.nacionalidade && touched.nacionalidade)}
                        helperText={errors.nacionalidade}
                        onChange={(e, v) => {
                          // console.log(v.props.value,'Valor selecionado')
                          setFieldValue('nacionalidade', v.props.value);
                          buscarProvincias(v.props.value)
                        }}
                        //onChange={handleChange}
                        onBlur={handleBlur} select
                        value={values.nacionalidade} margin="dense"
                        id="nacionalidade" fullWidth label="Nacionalidade">
                        {nacionalidades.map(opt => <MenuItem value={opt} key={opt}>{opt.name}</MenuItem>)}
                      </TextField>
                    </Grid>
                    <Grid item sm={4}>
                      <TextField
                        type="text" variant="outlined" name="naturalidade"
                        error={(errors.naturalidade && touched.naturalidade)}
                        helperText={errors.naturalidade} select
                        onChange={(e, v) => {
                          setFieldValue('naturalidade', v.props.value);
                          buscarCidades(v.props.value)
                        }}
                        value={values.naturalidade} margin="dense"
                        id="naturalidade" fullWidth label="Provincia">
                        {provinciasde.map(opt => <MenuItem value={opt} key={opt}>{opt.name}</MenuItem>)}
                      </TextField>
                    </Grid>
                    <Grid item sm={4}>
                      <TextField
                        type="text" variant="outlined" name="cidade"
                        error={(errors.cidade && touched.cidade)}
                        helperText={errors.cidade}
                        onChange={handleChange}
                        onBlur={handleBlur} select
                        value={values.nacionalidade} margin="dense"
                        id="cidade" fullWidth label="Cidade">
                        {cidades.map(opt => <MenuItem value={opt} key={opt}>{opt.name}</MenuItem>)}
                      </TextField>
                    </Grid>

                    <Grid item sm={4}>
                      <TextField
                        type="text" variant="outlined" name="telemovel"
                        error={(errors.telemovel && touched.telemovel)}
                        helperText={errors.telemovel}
                        onChange={handleChange}
                        onBlur={handleBlur}
                        value={values.telemovel} margin="dense"
                        id="telemovel" fullWidth label="Contacto" />
                    </Grid>
                    <Grid item sm={4}>
                      <TextField
                        type="text" variant="outlined" name="email"
                        error={(errors.email && touched.email)}
                        helperText={errors.email}
                        onChange={handleChange}
                        onBlur={handleBlur}
                        value={values.email} margin="dense"
                        id="email" fullWidth label="Email" />
                    </Grid>
                    <Grid item sm={4}>
                      <LocalizationProvider dateAdapter={AdapterDateFns}>
                        <Stack spacing={3}>
                          <DesktopDatePicker
                            label="Data de Nascimento"
                            name="dataNascimento"
                            format="dd/MM/yyyy"
                            value={selectedDateNasc}
                            // minDate={new Date('2017-01-01')}
                            onChange={(v) => {
                              setSelectedDateNasc(v);
                              setFieldValue('dataNascimento', format(v, 'yyyy-MM-dd'))
                            }}
                            KeyboardButtonProps={{
                              'aria-label': 'change date',
                            }}

                            renderInput={(params) => <TextField {...params} />}
                          />
                        </Stack>
                      </LocalizationProvider>
                    </Grid>
                    <Grid item sm={4}>
                      <TextField
                        type="text" variant="outlined" name="estado"
                        error={(errors.estado && touched.estado)}
                        helperText={errors.estado}
                        onChange={handleChange}
                        onBlur={handleBlur} select
                        value={values.estado} margin="dense"
                        id="estado" fullWidth label="Status">
                        {estados.map(opt => <MenuItem value={opt} key={opt}>{opt}</MenuItem>)}
                      </TextField>
                    </Grid>
                    <Grid item sm={4}>
                      <TextField
                        type="text" variant="outlined" name="regime"
                        error={(errors.regime && touched.regime)}
                        helperText={errors.regime}
                        onChange={handleChange}
                        onBlur={handleBlur} select
                        value={values.regime} margin="dense"
                        id="regime" fullWidth label="Regime">
                        {regimesServivo.map(opt => <MenuItem value={opt} key={opt}>{opt}</MenuItem>)}
                      </TextField>
                    </Grid>
                    <Grid item sm={4}>
                      <TextField
                        type="text" variant="outlined" name="sexo"
                        error={(errors.sexo && touched.sexo)}
                        helperText={errors.sexo}
                        onChange={handleChange}
                        onBlur={handleBlur} select
                        value={values.sexo} margin="dense"
                        id="sexo" fullWidth label="Sexo">
                        {sexos.map(opt => <MenuItem value={opt} key={opt}>{opt}</MenuItem>)}
                      </TextField>
                    </Grid>

                    <Grid item sm={4}>
                      <TextField
                        type="text" variant="outlined" name="tipoDocumento"
                        error={(errors.tipoDocumento && touched.tipoDocumento)}
                        helperText={errors.tipoDocumento}
                        onChange={handleChange}
                        onBlur={handleBlur} select
                        value={values.tipoDocumento} margin="dense"
                        id="tipoDocumento" fullWidth label="Tipo de documento">
                        {tiposDocumentos.map(opt => <MenuItem value={opt.values} key={opt}>{opt.label}</MenuItem>)}
                      </TextField>
                    </Grid>
                    <Grid item sm={4}>
                      <TextField
                        type="text" variant="outlined" name="numeroDocumento"
                        error={(errors.numeroDocumento && touched.numeroDocumento)}
                        helperText={errors.numeroDocumento}
                        onChange={handleChange}
                        onBlur={handleBlur}
                        value={values.numeroDocumento} margin="dense"
                        id="numeroDocumento" fullWidth label="Número do documento" />
                    </Grid>
                    <Grid item sm={4}>
                      <LocalizationProvider dateAdapter={AdapterDateFns}>
                        <Stack spacing={3}>

                          <DesktopDatePicker
                            label="Data de Emissao"
                            name="dataEmissao"
                            format="dd/MM/yyyy"
                            value={selectedDateEmissao}
                            // minDate={new Date('2017-01-01')}
                            onChange={(v) => {
                              setSelectedDateEmissao(v);
                              setFieldValue('dataEmissao', format(v, 'yyyy-MM-dd'))
                            }}
                            KeyboardButtonProps={{
                              'aria-label': 'change date',
                            }}

                            renderInput={(params) => <TextField {...params} />}
                          />

                        </Stack>
                      </LocalizationProvider>
                    </Grid>
                    <Grid item sm={4}>
                      <TextField
                        type="text" variant="outlined" name="nivelAcademico"
                        error={(errors.nivelAcademico && touched.nivelAcademico)}
                        helperText={errors.nivelAcademico}
                        onChange={handleChange}
                        onBlur={handleBlur} select
                        value={values.nivelAcademico} margin="dense"
                        id="nivelAcademico" fullWidth label="Nivel académico">
                        {niveisAcademicos.map(opt => <MenuItem value={opt.values} key={opt}>{opt.label}</MenuItem>)}
                      </TextField>
                    </Grid>
                    <Grid item sm={4}>
                      <TextField
                        type="text" variant="outlined" name="areaFormacao"
                        error={(errors.areaFormacao && touched.areaFormacao)}
                        helperText={errors.areaFormacao}
                        onChange={handleChange}
                        onBlur={handleBlur}
                        value={values.areaFormacao} margin="dense"
                        id="areaFormacao" fullWidth label="Area de Formacao" />
                    </Grid>
                    <Grid item sm={4}>
                      <TextField
                        type="text" variant="outlined" name="departamento"
                        error={(errors.departamento && touched.departamento)}
                        helperText={errors.departamento}
                        onChange={(e, v) => { cadeirasDepartamento = cadeiras.filter(cad => Boolean(cad.departamento)).filter(cad => cad.departamento.id == v.props.value.id); setFieldValue('departamento', v.props.value); }}
                        onBlur={handleBlur} select
                        value={values.departamento} margin="dense"
                        id="departamento" fullWidth label="Departamento">
                        {departamentos.map(opt => <MenuItem value={opt} key={opt}>{opt.nome}</MenuItem>)}
                      </TextField>

                    </Grid>
                    <Grid item sm={8}>

                          <Autocomplete onChange={(e, v) => { setFieldValue('cadeiras', v); }}
                        fullWidth multiple name="cadeiras"
                        getOptionLabel={option => option.nome}
                        options={cadeirasDepartamento} style={{ minWidth: 200 }}
                        renderInput={(params) => <TextField variant="outlined" margin="dense" label="Cadeiras a leccionar"
                          fullWidth {...params} placeholder="Seleccione uma opção" />}
                      />


                    </Grid>
                   
                    <Grid item sm={12} style={{ paddingTop: '10px', textAlign: 'right' }}>
                      <Button component={NavLink} to="/departamentos" color="secondary">Cancelar</Button> &nbsp;&nbsp;&nbsp;
                      <Button type="submit"
                        color="primary" variant="contained">Salvar</Button>
                    </Grid>

                  </Grid>
                </Form>
                )
              }}</Formik>
            <br />
          </Container>
        </Paper>
      </Container>)}
      
      </div>
    )
}